<template>
  <div class="wrapper">
    <div class="container">

      <div class="output">
        {{output}}
      </div>

      <div class="time__inp">

        <div class="timeset">
          <label for="hours">H</label>
          <input v-model="hours">
          <div class="inp__btns">
            <button @click="add('hours')">+</button>
            <button @click="decrease('hours')">-</button>
          </div>
        </div>

        <div class="timeset">
          <label for="minutes">M</label>
          <input v-model="minutes">
          <div class="inp__btns">
            <button @click="add('minutes')">+</button>
            <button @click="decrease('minutes')">-</button>
          </div>
        </div>

        <div class="timeset">
          <label for="seconds">S</label>
          <input v-model="seconds">
          <div class="inp__btns">
            <button @click="add('seconds')">+</button>
            <button @click="decrease('seconds')">-</button>
          </div>
        </div>

      </div>

      <div class="btns">
        <button @click="startTimer()" :disabled="isRunning">Start</button>
        <button @click="stopTimer()">Stop</button>
        <button @click="clearTimer()">Clear</button>
      </div>

    </div>
  </div>
</template>

<script>
import ring from '@/assets/sounds/ring.mp3'
export default {
  data(){
    return{
      isRunning: false,
      hours: 0,
      minutes: 0,
      seconds: 0,


      timeLeft: 0,
      intervalId: null,

      output: `00:00:00`
    }
  },
  methods:{
    playRing(){
      const audio = new Audio(ring)
      audio.play()
    },
    checkNaN(){
      if( isNaN(this.hours) || isNaN(this.minutes) || isNaN(this.seconds)){
        alert('Only integer values')
        this.clearTimer()
      }else{
        this.hours = Number(this.hours)
        this.minutes = Number(this.minutes)
        this.seconds = Number(this.seconds)
      }
    },
    add(time){
      this[time] ++
    },
    decrease(time){
      if (this[time] !== 0){
        this[time] --
      }
    },
    startTimer(){
      this.checkNaN()
      this.isRunning = true
      this.timeLeft = (this.hours * 60 * 60) + (this.minutes * 60) + (this.seconds)
      this.intervalId = setInterval(this.updateTimer, 1000)
    },
    updateTimer(){
      let hours = Math.floor(this.timeLeft / 3600);
      let minutes = Math.floor((this.timeLeft % 3600) / 60);
      let seconds = this.timeLeft % 60;

      this.output = `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`


      if (this.timeLeft === 0) {
        clearInterval(this.intervalId);
        this.playRing()
        this.isRunning = false
      } else {
        this.timeLeft--;
      }

    },
    stopTimer(){
      this.isRunning = false
      clearInterval(this.intervalId)
    },
    clearTimer(){
      this.isRunning = false
      clearInterval(this.intervalId)
      this.hours = 0
      this.minutes = 0
      this.seconds = 0
      this.output = `00:00:00`
    }
  }
}
</script>

